.header {
  width: 100%;
  background-color: rgba(128, 128, 128, .4);
  height: 10vh;
  border-bottom: 1px solid grey;

  .nav {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    //border: 1px dashed #3c3c3c;

    &__logo {
      display: inline-block;
      font-size: 0;
      img {
        height: 9vh;
        width: auto;
        margin-left: 1vw;
      }
    }

    &__title {
      font-size: 1.5rem;
      //width: 25vw;
      //margin-left: 1vw;
    }

    &__menu {
      width: 60vw;
      height: 10vh;
      display: flex;
      flex-grow: 1;
      flex-shrink: 1;
      flex-flow: row wrap;
      justify-content: center;
      align-items: center;

      li {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 50%;
        background-color: #dddddd;
        text-align: center;
        color: #000;
        font-size: 1.2rem;
        padding: 0.5vw;
        margin: 0 0.2em;
        //width: 7vw;
        a {
          display: inline-block;
          text-decoration: none;
          color: #000;
          outline: none;
          font-size: 1.3rem;
        }
      }
    }

    .login-register-btn {
      background-color: rgba(255, 255, 255, 0.4);
      margin-right: 1vw;
      padding: 5px;
      height: 8vh;
      transition: 0.3s all;

      &:hover {
        cursor: pointer;
        transform: scale(1.1);
      }

      a {
        display: inline-block;
        height: inherit;


        img {
          display: inline-block;
          font-size: 0;
          height: inherit;
          width: auto;

        }
      }

    }
  }
}





