.main-cnt {
  width: 100%;
  height: 100vh;
  background-image: url("../img/question-mark-1872665_1920.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  text-align: center;
  color: white;
  h1 {
    padding-top: 3vh;
  }
}

.main-slider {
  padding: 2%;
  background-color: rgba(0, 0, 0, .5);
  margin: 1vh auto 0 auto;
  width: 60%;
  height: 60vh;
  border: 2px solid grey;
  //border-radius: 150px;
}

.login-register {
  //z-index: 999;
  //display: none;
  position: absolute;
  margin: auto;
  left: 0;
  right: 0;
  top: 0;
  bottom: 10vh;
  width: 400px;
  height: 390px;
  background-color: #4a4a4a;

  form {
    border: 12px solid #4a4a4a;
    //background-color: #dd521b;
    background: linear-gradient(#ff884a, #dd521b);
    padding: 20px;
    display: flex;
    flex-flow: column wrap;
    font-size: 1.4rem;
    margin-bottom: 10px;

    p {
      display: flex;
      flex-flow: column;
      justify-content: center;
      align-items: center;
    }

    input:not([id='rememberme']) {
      height: 40px;
      width: 200px;
      padding: 5px;
      font-size: 1.2rem;
      color: #000;
      margin: 10px 0;
    }

    .login-submit {
      input {
        height: inherit;
        background-color: #4a4a4a;
        border: none;
        margin: 20px 15px 5px 15px;
        padding: 20px;
        color: #fff;
        font-weight: bold;
        transition: 0.3s all;

        &:hover {
          cursor: pointer;
          background-color: lighten(#4a4a4a, 10%);
        }
      }
    }

    .login-remember label {
      font-size: 1rem;
    }

  }

  a {
    color: #ff884a;
    font-size: 1.5rem;
    font-weight: bold;
    text-decoration: none;
    transition: 0.3s all;

    &:hover {
     color: #dd521b;
    }
  }

}

.hidden {
  display: none;
}

.showed {
  display: block;
}